import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Project } from '../project.model';


@Injectable({
  providedIn: 'root'
})
export class ProjectService {

  private httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
    withCredentials: true
  };

  private url = 'api/projects';

  constructor( private http: HttpClient ) { }

  getProjects(): Observable<Project[]> {
    return this.http.get<Project[]>(`${this.url}`);
  }

  getProject(projectId: string): Observable<Project> {
    return this.http.get<Project>(`${this.url}/${projectId}`);
  }

  addProject(project: Project): Observable<any> {
    return this.http.post(this.url, project, this.httpOptions);
  }

  updateProject(projectId: string, userId: string) {
    const data = { projectId: projectId, userId: userId };
    return this.http.post(`${this.url}/${projectId}`, data, this.httpOptions);
  }

  getProjectEmp(projectId: string) {
    return this.http.get(`${this.url}/${projectId}/employees`, this.httpOptions);
  }

  updateProjectEmp(oldProjectId: string, newProjectId: string, userId: string) {
    const data = { oldProjectId: oldProjectId, newProjectId: newProjectId, userId: userId };
    return this.http.post(`${this.url}/${newProjectId}/employees`, data, this.httpOptions);
  }
}
