import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of, Subject } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';

import { Admin } from '../models/admin.model';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
    withCredentials: true
  };

  private url = 'api/auth';

  loggedIn: Subject<boolean>;

  constructor( private http: HttpClient,
               private toastr: ToastrService
             ) {
               this.loggedIn = new Subject;
               this.getLogin();
              }

  getLogin() {
    this.http.get(`${this.url}/login`, this.httpOptions)
      .subscribe((resp: any) => {
        this.loggedIn.next(resp.loggedIn);
    });
  }

  doLogin(admin: Admin) {
    this.http.post(`${this.url}/login`, admin, this.httpOptions)
      .subscribe((resp: any) => {
        if (resp.success) {
          this.toastr.success('You are logged in', 'Welcome');
          this.loggedIn.next(true);
        } else {
          this.toastr.error(resp.message, 'Oops...');
          this.loggedIn.next(false);
        }
      });
  }

  logout() {
    this.http.post(`${this.url}/logout`, {}, {
      withCredentials: true
    }).subscribe(() => {
      this.loggedIn.next(false);
      this.toastr.error('You have been logged-out', 'Logged Out');
    });
  }

  loginCheck(): any {
    return this.http.get(`${this.url}/login`, this.httpOptions);
  }


}
