import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of, Subject } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Employee } from '../employee.model';


@Injectable({
  providedIn: 'root'
})
export class EmployeeService {

  private httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
    withCredentials: true
  };

  private url = 'api/employees';

  constructor( private http: HttpClient ) {
   }


    getEmployees(): Observable<Employee[]> {
      return this.http.get<Employee[]>(this.url);
    }


    getEmployee(id: string): Observable<Employee> {
      return this.http.get<Employee>(`${this.url}/${id}`);
    }


    updateEmployee(id: string, employee: Employee): Observable<any> {
      return this.http.put(`${this.url}/${id}`, employee, this.httpOptions);
    }


    addEmployee(employee: Employee): Observable<any> {
      return this.http.post(this.url, employee, this.httpOptions);
    }

    deleteEmployee(id: string) {
      return this.http.delete(`${this.url}/${id}`, this.httpOptions);
    }

    searchEmployees(term: string): Observable<Employee[]> {
      if (!term.trim()) {
        return of([]);
      }
      return this.http.get<Employee[]>(`${this.url}/search/${term}`);
    }



}
