import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

import { EmployeeService } from '../shared/employees.service';
import { ProjectService } from '../shared/projects.service';
import { AuthService } from '../shared/auth.service';

import { Employee } from '../employee.model';
import { Project } from '../project.model';

@Component({
  selector: 'app-add-emp',
  templateUrl: './add-emp.component.html',
  styleUrls: ['./add-emp.component.css']
})
export class AddEmpComponent implements OnInit {
  name: string;
  designation: string;
  project: string;
  skills: string;

  projectsList: Project[];

  constructor( private employeeService: EmployeeService,
               private authService: AuthService,
               private router: Router,
               private toastr: ToastrService,
               private projectService: ProjectService
             ) { }


  ngOnInit() {
    this.authCheck();
    this.getProjects();
  }

  getProjects() {
    this.projectService.getProjects()
      .subscribe((projects) => {
        this.projectsList = projects;
      });
  }

  onSubmit(): void {
    const newEmployee = new Employee;
    newEmployee.name         = this.name;
    newEmployee.designation  = this.designation;
    newEmployee.project      = this.project;
    newEmployee.skills       = this.skills;

    this.employeeService.addEmployee(newEmployee)
      .subscribe((res) => {
        this.toastr.success(res.message, 'Done!');
        this.router.navigate(['/home']);
      }, (errorRes) => {
        this.toastr.error(errorRes.error.message, 'We are sorry...');
      });
  }

  newEmp(): void {
    this.name = null;
    this.designation = null;
    this.project = null;
    this.skills = null;
  }

  authCheck() {
    this.authService.loginCheck()
      .subscribe((resp) => {
        if (resp.loggedIn === false) {
          this.router.navigate(['/home']);
          this.toastr.error('No permission to add employees', 'Access Denied');
          }
      });
    }

}
