import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { ToastrService } from 'ngx-toastr';

import { Employee } from '../../employee.model';
import { Project } from '../../project.model';


import { EmployeeService } from '../../shared/employees.service';
import { ProjectService } from '../../shared/projects.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  @Input() selectedEmployee: Employee;
  @Output() updatedEmp     = new EventEmitter<Employee>();
  projectsList: Project[] = [];
  selectedProject: Project;


  constructor(private employeeService: EmployeeService,
              private projectService: ProjectService,
              private route: ActivatedRoute,
              private router: Router,
              private toastr: ToastrService
    ) { }

  ngOnInit() {
    this.getProjects();
  }

  getProjects(): void {
    this.projectService.getProjects()
      .subscribe(projects => this.projectsList = projects);
  }

  makeAvailable(): void {
    this.selectedEmployee.available = true;
    this.selectedEmployee.project = null;
    this.updateEmployee();
  }

  assignProject(): void {
    this.updateProjectEmp();
    this.updateEmployee();
  }

  updateEmployee(): void {
    const empId     = this.route.snapshot.params.id;
    const projectId = this.selectedProject._id;
    this.selectedEmployee.project = projectId;

    this.employeeService.updateEmployee(empId, this.selectedEmployee)
      .subscribe((res) => {
        this.toastr.success(res.message, 'Done!');
        this.updatedEmp.emit(this.selectedEmployee);
      }, (errorRes) => {
        this.toastr.error(errorRes.error.message, 'We are sorry...');
      });
  }

  updateProjectEmp(): void {
    const newProjectId = this.selectedProject._id;
    const oldProjectId = this.selectedEmployee.project;
    const userId       = this.selectedEmployee._id;
    this.projectService.updateProjectEmp(oldProjectId, newProjectId, userId)
      .subscribe((res: any) => {
        this.toastr.success(res.message, 'Done!');
      }, (errorRes) => {
        this.toastr.error(errorRes.error.message, 'We are sorry...');
      });
  }
}
