import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';

import { Employee } from '../employee.model';
import { Project } from '../project.model';


import { EmployeeService } from '../shared/employees.service';
import { ProjectService } from '../shared/projects.service';
import { AuthService } from '../shared/auth.service';


@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.css']
})
export class DetailsComponent implements OnInit {

  selectedEmployee: Employee = new Employee;
  currentProject: Project = new Project;

  loggedIn;
  message: string = null;
  showEdit = false;

  constructor( private employeeService: EmployeeService,
               private projectService: ProjectService,
               private authService: AuthService,
               private route: ActivatedRoute,
               private router: Router,
               private toastr: ToastrService,
               private http: HttpClient
             ) { }

  ngOnInit() {
    this.getEmployee();
  }

  getEmployee(): void {
    const id = this.route.snapshot.params.id;

    this.employeeService.getEmployee(id)
      .subscribe((employee) => {
        this.selectedEmployee = employee;
        this.getProject();
      }, (errorRes) => {
        this.toastr.error(errorRes.error.message, 'We are sorry...');
      });
  }

  getProject(): void {
    const projectId = this.selectedEmployee.project;
    if (!projectId) {
      this.currentProject.name = 'none';
      this.selectedEmployee.available = true;
      this.message = 'This employee is available.';

    } else {
      this.projectService.getProject(projectId)
      .subscribe(project => {
        this.currentProject = project;
      }, (errorResp) => {
        this.toastr.error(errorResp.message, 'We are sorry...');
      });
    }

  }

  onEdit(): void {
    this.authService.loginCheck()
      .subscribe(resp => {
      this.loggedIn = resp.loggedIn;

        if (this.loggedIn) {
          if (this.showEdit === false) {
            this.showEdit = true;
          } else {
            this.showEdit = false;
          }
        } else {
          this.toastr.error('No permission to edit', 'Access Denied');
          this.router.navigate(['/home']);
        }
      });
  }

  onUpdate(employee: Employee): void {
    this.showEdit = false;
    this.selectedEmployee = employee;
    this.getProject();
  }

}
