import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

import { EmployeeService } from '../shared/employees.service';
import { ProjectService } from '../shared/projects.service';
import { AuthService } from '../shared/auth.service';

import { Employee } from '../employee.model';
import { Project } from '../project.model';


@Component({
  selector: 'app-add-project',
  templateUrl: './add-project.component.html',
  styleUrls: ['./add-project.component.css']
})
export class AddProjectComponent implements OnInit {

  name: string;
  designation: string;

  constructor(private employeeService: EmployeeService,
              private authService: AuthService,
              private router: Router,
              private toastr: ToastrService,
              private projectService: ProjectService
              ) { }

  ngOnInit() {
    this.authCheck();
  }


  onSubmit() {
    const newProject = new Project;
    newProject.name        = this.name;
    newProject.designation = this.designation;
    newProject.developers  = [];
    newProject.finished    = false;

    this.projectService.addProject(newProject)
      .subscribe((res) => {
        this.toastr.success(res.message, 'Done!');
        this.router.navigate(['/home']);
      }, (errorRes) => {
        this.toastr.error(errorRes.error.message, 'We are sorry...');
      });
  }


  authCheck() {
    this.authService.loginCheck()
      .subscribe((resp) => {
        if (resp.loggedIn === false) {
          this.router.navigate(['/home']);
          this.toastr.error('No permission to add projects', 'Access Denied');
          }
      });
  }

  newProject(): void {
    this.name = null;
    this.designation = null;
  }

}
